.advantages {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  text-align: left;
}

.advantages__item {
  position: relative;
  width: 40%;
  padding-left: 50px;
  margin-bottom: 20px;
  margin-right: 34px;

  @media (max-width: 979px) {
    width: 45%;
  }

  @media (max-width: 759px) {
    font-size: 14px;
    line-height: 20px;
    margin-right: 26px;
  }

  @media (max-width: 599px) {
    width: 100%;
    margin-right: 0;
    padding-left: 30px;
  }
}

.advantages__icon {
  display: flex;
  align-items: center;
  justify-content: center;

  position: absolute;
  top: 6px;
  left: 0;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background: rgba(228, 29, 34, 0.5);

  @media (max-width: 599px) {
    top: 0;
  }
}

.advantages__icon span {
  font-weight: bold;
  font-size: 18px;
  line-height: 46px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}
